<template>
  <div class="movie-view has-header">
    <scroller title="" type="hasCover" :items="novel"></scroller>
    <scroller title="" type="hasCover" :items="reality"></scroller>
    <scroller title="" type="hasCover" :items="travel">
      <div class="promItem" slot="promItem">
        <img class="corver" src="../assets/book_zw.jpg" alt="">
        <div class="content">
          <span class="price"> 68</span>
          <p class="name"></p>
          <p class="info">3000</p>
        </div>
      </div>
    </scroller>
    <scroller title="" type="onlyString" :items="bookTags"></scroller>

    <types></types>
    <download-app></download-app>
  </div>
</template>

<script>
import { mapState } from 'vuex'

import Scroller from '../components/Scroller.vue'
import Types from '../components/Types.vue'
import DownloadApp from '../components/DownloadApp.vue'

export default {
  name: 'book-view',
  components: { Scroller, Types, DownloadApp },
  data () {
      return {}
  },
  computed: {
    ...mapState({
        novel: state => state.book.novel,
        reality: state => state.book.reality,
        travel: state => state.book.travel,
        bookTags: state => state.book.bookTags
    })
  },
 methods: {
     getBook () {
         this.$store.dispatch('getBook')
     }
 },
 created () {
     this.getBook()
 }
}
</script>

<style scoped>
.promItem {
  overflow: hidden;
  margin: 1.6rem 1.8rem 0.8rem 1.6rem;
}

.corver {
  float: left;
  width: 10rem;
  margin-right: 1.5rem;
}

.content {
  margin-right: 1rem;
}

.name {
  font-size: 2rem;
  color: #494949;
  margin: 1rem;
  max-width: 100%;
  line-height: 2.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}

.price {
  float: right;
  color: #E76648;
  font-size: 1.6rem;
  line-height: 2.2rem;
}

.info {
  font-size: 1.3rem;
  font-weight: 300;
  line-height: 1.5;
  color: #9B9B9B;
}
</style>



